import React, { Component } from 'react';
import { Button } from 'react-bootstrap'
import './SignUp.css'
import image from '../assets/web.png'
import main from '../assets/main.png'
import { Link, useHistory } from "react-router-dom";
import Footer from '../Footer/Footer'


class Home extends Component {
    constructor(props) {
        super(props);
    }
    routeChange = () => {
        let path = `/account/home`;
        this.props.history.push(path);
    }
    login = () => {
        let path = `/account/login`;
        this.props.history.push(path);
    }
    render() {
        return (
            <div className="container-fluid p-0">
                <div className="row bg-img ">
                    <div className="col-lg-12 col-sm-12">
                        <div className="container-fluid ">
                            <div className="row">
                                <div className="col-lg-4 col-sm-0 text-center" ></div>
                                <div className="col-lg-4 col-sm-12 text-center" style={{ marginTop: '3%' }}>
                                    <img src={main} width="250px" height="45px"></img>
                                </div>
                                <div className="col-lg-4 col-sm-0 text-center" ></div>
                            </div>
                            <div className="row mt-4">
                                <div className="col-lg-4 col-sm-0 text-center" style={{ marginTop: 0 }}></div>
                                <div className="col-lg-4 col-sm-12 text-center" style={{ marginTop: 0 }}>
                                    <h5 style={{ color: "white" }}>Grow your business</h5>
                                </div>
                                <div className="col-lg-4 col-sm-0 text-center" style={{ marginTop: 0 }}></div>
                            </div>
                            <div className="row">
                                <div className="col-12 text-center buttons inner-pad">
                                    <button onClick={this.routeChange} className="btn" ref="/Home" style={{ background: "#2101F0", width: "210px", color: "white" }} >
                                        Create account
                                </button>
                                </div>
                            </div>
                            <div className="row pt-2 pb-1">
                                <div className="col-12 text-center">
                                    <h6>or</h6>
                                </div>
                            </div>
                            <div className="row ">
                                <div className="col-12 text-center buttons" style={{ paddingLeft: 40, paddingRight: 40 }}>
                                    <button onClick={this.login} className="btn " style={{ background: "white", width: "210px", color: "black" }} >
                                        Login
                             </button>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}
export default Home;